/* set root style */

* {
  scroll-behavior: smooth;
}

body {
  width: 100%;
  overflow-x: hidden;
}

p {
  font-family: Ubuntu, sans-serif;
  font-size: 13px;
  font-weight: 400;
}

a {
  font-family: Ubuntu, sans-serif;
  font-size: 10px;
}

h1 {
  font-family: Heebo, sans-serif;
  font-size: 25px;
  font-weight: 600;
}

h2 {
  font-family: Roboto, sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.btn {
  border-radius: 5px;
  --tw-bg-opacity: 1;
  background-color: rgb(0 0 0 / var(--tw-bg-opacity));
  padding-top: 7px;
  padding-bottom: 7px;
  padding-left: 20px;
  padding-right: 20px;
  font-family: Heebo, sans-serif;
  font-weight: 600;
  --tw-text-opacity: 1;
  color: rgb(255 255 255 / var(--tw-text-opacity));
}

.tag {
  border-radius: 4px;
  --tw-bg-opacity: 1;
  background-color: rgb(15 23 42 / var(--tw-bg-opacity));
  padding-left: 10px;
  padding-right: 10px;
  padding-top: 3px;
  padding-bottom: 3px;
  font-size: 8px;
}

.headline {
  font-family: Heebo, sans-serif;
  font-size: 30px;
  font-weight: 800;
}

@media (min-width: 1280px) {
  .headline {
    font-size: 50px;
  }
}

/* hiasan */

.hiasan-about {
  position: absolute;
  right: 45%;
  height: 20px;
}

@media (min-width: 768px) {
  .hiasan-about {
    top: -10px;
    height: 30px;
  }
}

@media (min-width: 1024px) {
  .hiasan-about {
    right: 48%;
    top: -13px;
    height: 35px;
  }
}

.hiasan {
  position: absolute;
  right: -110%;
  top: 200px;
  z-index: -10;
  width: 150%;
  opacity: 0.1;
}

@media (min-width: 768px) {
  .hiasan {
    right: -40%;
    width: 70%;
  }
}

@media (min-width: 1024px) {
  .hiasan {
    right: -25%;
    width: 50%;
  }
}

.hiasan-repo {
  position: absolute;
  top: 20px;
  left: -15%;
  width: 50%;
  opacity: 0.3;
}

@media (min-width: 768px) {
  .hiasan-repo {
    left: -5%;
    width: 30%;
  }
}

@media (min-width: 1024px) {
  .hiasan-repo {
    width: 25%;
  }
}

